.App {
  color: aliceblue;
  font-family: monospace;
}

body{
  background-color: black;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
  font-family: monospace;
}

a:hover{
  color: aqua;
}

a:visited{
  color: darkolivegreen;
}

.book-wraper{
  margin: 10px;
  display: flex;
  flex-wrap: wrap;
  flex: content;
  justify-content: center;
  text-align: center;
  /* max-width: 1024px; */
}

.book-card {
	width: 550px;
	height: 200px;
	margin-top: 5px;
	border: 1px gray solid;
	padding: 5px;
	border-radius: 10px;
  margin-left: 5px;
  animation-duration: 1s;
  animation-name: card_animation;
  overflow: hidden;
}

@keyframes card_animation {
  from {
    margin-top: 100px;
    opacity: 0;
  }
  to {
    margin-top: 5px;
    opacity: 1;
  }
}

.book-card-img{
  text-align: left;
  float: left;
  padding-left: 5px;
  padding-right: 5px;
}

.book-link-pdf{
  margin-left: 5px;
}

.book-link-epub{
  margin-left: 5px;
}

.book-card-img-tag{
  height: 200px;
}

.book-card-misc{
  text-align: right;
}

.book-card-misc span{
  color: cadetblue;
}

.book-card-tite{
  font-weight: 600;
  height: 35px;
}

.card-span-title{
  font-family: monospace;
  font-size: large;
}

.book-card-subtitle{
  font-size: small;
  font-weight: 600;
}

.book-card-description{
  font-size: medium;
  text-align: justify;
  text-justify: inter-word;
  padding-right: 5px;
  padding-top: 2px;
  padding-bottom: 2px;
  height: 125px;
  overflow: hidden;
  position: relative;
  color: grey;
  max-width: 520px;
}
.book-desc-span{
  height: 125px;
  position: absolute;
  min-width: 300px;
}

.book-desc-span:hover{
  font-size: larger;
  font-weight: 100;
  color: lightgray;
  /* animation: scrollUp 40s 5s infinite linear; */
  overflow-y: scroll;
  text-align: left;
  scrollbar-width: thin;
  scrollbar-color: gray black ;
  scroll-behavior: smooth;
}

/* @keyframes scrollUp {
  from {
    transform: translateY(0);
  }
  to {
    transform: translateY(-110%);
  }
} */

.book-card-download{
  text-align: right;
  padding-top: 5px;
}

.search-wraper{
  display: inline-block;
  text-align: center;
  margin-top: 20px;
  margin-bottom: 20px;
  width: 100%;
}

.search-form {
  width: 100%;
  display: inline-block;
  justify-content: center;
}

  .search-input {
    min-width: 400px;
    max-width: 600px;
    background-color: #000;
    color: #fff;
    height: 25px;
    font-size: 18px;
    border-radius: 15px;
    border: 1px white solid;
    padding-left: 10px;
    padding-right: 10px;
  }